import userMixin from '@/mixins/user.js';
import adminMixin from '@/mixins/admin.js'

export default {
    data() {
        return {
            accountPlan: null, 
            balance: null,
            requirement: null,
            saved: false
        }
    },
    mixins: [
        userMixin,
        adminMixin
        ],
    methods: {
        submitChanges() {
            const {
                accountPlan, 
                balance,
                requirement,
                transactions,
                notifications
            } = this;

            const changes = {
                accountPlan, 
                balance,
                requirement,
                transactions,
                notifications
            };

            this.submit(changes);
        },
        submit(changes) {
            const user_token = JSON.parse(localStorage.getItem('nordtokenxtxtxt'));
            const user_id = this.$route.query.id;
            
            console.log(changes)
            fetch(`${this.baseUrl}/api/patchuser?userid=${user_id}`, {
                method: "PATCH",
                body: JSON.stringify(changes),
                headers: {
                    "Content-type": "application/json; charset=UTF-8",
                    "Authorization": user_token
                }
            })
            .then(response => response.json())
            .then(json => {
                console.log(json);
                this.saved = true;

                setTimeout(() => {
                    this.saved = false
                }, 8000);
            }).catch(err => {
                console.log(err)
            })
        }
    },
    beforeMount() {
        this.getUsers();
    },
    mounted() {
        console.log(this.$route.query.id);
        console.log(this.user, 'user here');
        
        if (this.user !== null) {
            this.accountPlan = this.user.accountPlan; 
            this.balance = this.user.balance;
        }
    },
    computed: {
        transactions() {
            return this.$store.getters['userEdit/transactions']
        },
        notifications() {
            return this.$store.getters['userEdit/notifications']
        },
        user() {
            return this.$store.getters.users ? this.$store.getters.users.filter(user => `${user._id}` == this.$route.query.id)[0] : null;
        }
    }
}
